
Template.__checkName("self");
Template["self"] = new Template("Template.self", (function() {
  var view = this;
  return Blaze.Each(function() {
    return Spacebars.call(view.lookup("self"));
  }, function() {
    return [ "\n        ", Blaze.If(function() {
      return Spacebars.call(view.lookup("isAdmin"));
    }, function() {
      return [ "\n            ", HTML.H1({
        "class": "text-danger"
      }, "尊贵的管理员账户！"), "\n        " ];
    }, function() {
      return [ "\n            ", HTML.H4({
        "class": "text-info"
      }, "普通用户。"), "\n        " ];
    }), "\n        ", HTML.DL({
      "class": "dl-horizontal"
    }, "\n            ", HTML.DT("用户名"), HTML.DD(Blaze.View("lookup:user.username", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("user"), "username"));
    })), "\n            ", HTML.DT("注册邮箱"), HTML.DD(Blaze.View("lookup:user.emails.0.address", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("user"), "emails", "0", "address"));
    })), "\n            ", HTML.DT("总积分"), HTML.DD(Blaze.View("lookup:totalScore", function() {
      return Spacebars.mustache(view.lookup("totalScore"));
    })), "\n            ", HTML.DT("今日积分"), HTML.DD(Blaze.View("lookup:todayScore", function() {
      return Spacebars.mustache(view.lookup("todayScore"));
    })), "\n            ", HTML.DT("好友总数"), HTML.DD(Blaze.View("lookup:totalFriend", function() {
      return Spacebars.mustache(view.lookup("totalFriend"));
    })), "\n            ", HTML.DT("排名"), HTML.DD(Blaze.View("lookup:rank", function() {
      return Spacebars.mustache(view.lookup("rank"));
    })), "\n            ", HTML.DT("注册时间"), HTML.DD(Blaze.View("lookup:time", function() {
      return Spacebars.mustache(view.lookup("time"));
    })), "\n        "), "\n    " ];
  });
}));
